
@mixin desk {
    @media (max-width: 1200px) {
        @content;
    }
}

@mixin desk-sm {
    @media (max-width: 992px) {
        @content;
    }
}
@mixin tab {
    @media (max-width: 768px) {
        @content;
    }
}

@mixin mobile {
    @media (max-width: 575px) {
        @content;
    }

}